let caroSlider1 = null;
let caroAutoplay1 = null;
const BREAKPOINT = 1120;
const TIMER = 4000;

const initGradeSlider = () => {
  // Если слайдер уже существует или экран слишком большой - выходим
  if (caroSlider1 || window.innerWidth >= BREAKPOINT) return;
  
  const sliderContainer1 = document.querySelector('.card__slider');
  if (!sliderContainer1) return;

  // Инициализация слайдера
  caroSlider1 = new MicroSlider(sliderContainer1, { 
    indicators: true, 
    indicatorText: '',
    transitionDuration: 1000, 
    easing: 'ease-in-out'
  });

  // Автопрокрутка
  // caroAutoplay1 = setInterval(() => caroSlider1.next(), TIMER);

  // Обработчики событий
  const hammer = new Hammer(sliderContainer1);
  
  const stopAutoplay = () => {
    clearInterval(caroAutoplay1);
  };
  
  const restartAutoplay = () => {
    stopAutoplay();
    // caroAutoplay1 = setInterval(() => caroSlider1.next(), TIMER);
  };

  sliderContainer1.addEventListener('click', stopAutoplay);
  hammer.on('tap', stopAutoplay);
  hammer.on('swipe', restartAutoplay);
};

const destroyGradeSlider = () => {
  if (!caroSlider1) return;

  // Очистка интервала
  clearInterval(caroAutoplay1);
  caroAutoplay1 = null;

  // Уничтожение слайдера
  if (typeof caroSlider1.destroy === 'function') {
    caroSlider1.destroy();
  }
  caroSlider1 = null;
};

const checkGradeSlider = () => {
  if (window.innerWidth < BREAKPOINT) {
    initGradeSlider();
  } else {
    destroyGradeSlider();
  }
};

// Добавляем обработчик ресайза с троттлингом
let resizeTimeout;
window.addEventListener('resize', () => {
  clearTimeout(resizeTimeout);
  resizeTimeout = setTimeout(checkGradeSlider, 100);
});

// Инициализация при загрузке
document.addEventListener('DOMContentLoaded', checkGradeSlider);

export { initGradeSlider, destroyGradeSlider, checkGradeSlider };