const slider = document.querySelector('[data-slider="tape"]');

const initInfoSlider = () => {
  if (!slider) {
    return;
  }

  const swiper = new Swiper(slider, {
    slidesPerView: 'auto',
    
    spaceBetween: 8,
    grabCursor: true,
   
    autoplay: {
      delay: 500,           
      disableOnInteraction: false,
    },
    speed: 2800,          
    centeredSlides: true,
  });
};

export {initInfoSlider};
